<!-- src/views/StoreView.vue -->
<template>
  <section>
    <div v-if="error" class="alert alert-error">
      {{ error }}
    </div>

    <div v-if="loading" class="loading">

      Cargando tiendas...

    </div>

    <div v-else>
      <!-- Sin ?store: elegir tienda -->
      <div v-if="!storeParam" class="store-selector">
        <h2>Elige una tienda</h2>
        <p>
          Puedes usar la URL con <code>?store=tech</code> o
          <code>?store=fashion</code>, o haz clic en una tarjeta:
        </p>

        <div class="store-grid">
          <article
            v-for="store in stores"
            :key="store.slug"
            class="store-card"
            @click="goToStore(store.slug)"
          >
            <StoreHeader :store="store" />
          </article>
        </div>
      </div>

      <!-- Con ?store: vista de tienda -->
      <div v-else>
        <button class="btn-link" @click="clearStore">
          ← Volver a selección de tiendas
        </button>

        <div v-if="currentStore">
          <StoreHeader :store="currentStore" :show-description="true" />

          <h2 class="products-title">Productos</h2>
          <div class="products-grid">
            <ProductCard
              v-for="product in currentStore.products"
              :key="product.id"
              :product="product"
            />
          </div>
        </div>

        <div v-else class="alert alert-warning">
          No se encontró la tienda <strong>{{ storeParam }}</strong>. Elige una tienda válida.
          
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { mapState, mapGetters, mapActions } from "vuex";
import StoreHeader from "@/components/StoreHeader.vue";
import ProductCard from "@/components/ProductCard.vue";

export default {
  name: "StoreView",
  components: {
    StoreHeader,
    ProductCard,
  },
  computed: {
    ...mapState("stores", ["stores", "currentStore", "loading", "error"]),
    ...mapGetters("stores", ["hasStores"]),
    storeParam() {
      return this.$route.query.store || null;
    },
  },
  watch: {
    // Cuando cambie ?store, volvemos a seleccionar
    storeParam: {
      immediate: true,
      handler(newValue) {
        if (!this.hasStores) return;
        if (newValue) {
          this.selectStoreBySlug(newValue);
          this.updateTitle();
        } else {
          this.$store.commit("stores/SET_CURRENT_STORE", null);
          this.$router.currentRoute.value.meta.title = "Multi Tienda Vue";
          document.title = "Multi Tienda Vue";
        }
      },
    },
    currentStore(store) {
      // Actualizamos meta title cuando se cargue la tienda
      if (store) {
        this.updateTitle();
      }
    },
  },
  created() {
    // Cargamos tiendas al entrar (si no estaban ya)
    if (!this.hasStores) {
      this.fetchStores().then(() => {
        if (this.storeParam) {
          this.selectStoreBySlug(this.storeParam);
          this.updateTitle();
        }
      });
    } else if (this.storeParam) {
      this.selectStoreBySlug(this.storeParam);
      this.updateTitle();
    }
  },
  methods: {
    ...mapActions("stores", ["fetchStores", "selectStoreBySlug"]),
    goToStore(slug) {
      this.$router.push({ name: "Store", query: { store: slug } });
    },
    clearStore() {
      this.$router.push({ name: "Store", query: {} });
    },
    updateTitle() {
      if (!this.currentStore) return;
      const base = "Multi Tienda Vue";
      const title = `${this.currentStore.name} - ${base}`;
      this.$router.currentRoute.value.meta.title = title;
      document.title = title;
    }
  }
}
</script>

<style scoped>
.loading {
  padding: 1rem;
}

.alert {
  padding: 0.75rem 1rem;
  border-radius: 4px;
  margin-bottom: 1rem;
  font-size: 0.95rem;
}

.alert-error {
  background: #ffebee;
  border: 1px solid #f44336;
  color: #b71c1c;
}

.alert-warning {
  background: #fff8e1;
  border: 1px solid #ffc107;
  color: #795548;
}

.store-selector h2 {
  margin-top: 0;
}

.store-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
}

.store-card {
  background: #fff;
  border-radius: 8px;
  padding: 1rem;
  cursor: pointer;
  transition: transform 0.1s ease, box-shadow 0.1s ease;
  border: 1px solid #e0e0e0;
}

.store-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.06);
}

.btn-link {
  border: none;
  background: none;
  padding: 0;
  margin-bottom: 1rem;
  font-size: 0.9rem;
  cursor: pointer;
  text-decoration: underline;
}

.products-title {
  margin-top: 1.5rem;
}

.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
}
</style>
