<!-- src/components/StoreHeader.vue -->
<template>
  <header class="store-header" :style="headerStyle">
    <div class="store-logo" v-if="store.logoBase64">
      <img :src="store.logoBase64" :alt="store.name" />
    </div>
    <div class="store-info">
      <h2 class="store-name">{{ store.name }}</h2>
      <p v-if="showDescription && store.description" class="store-description">
        {{ store.description }}
      </p>
      <p v-else-if="!showDescription && store.description" class="store-description truncated">
        {{ store.description }}
      </p>
    </div>
  </header>
</template>

<script>
export default {
  name: 'StoreHeader',
  props: {
    store: {
      type: Object,
      required: true
    },
    showDescription: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    headerStyle() {
      return {
        borderBottom: `3px solid ${this.store.primaryColor || '#42b883'}`
      }
    }
  }
}
</script>

<style scoped>
.store-header {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.store-logo img {
  width: 56px;
  height: 56px;
  object-fit: cover;
  border-radius: 50%;
  border: 2px solid #e0e0e0;
}

.store-name {
  margin: 0;
}

.store-description {
  margin: 0.25rem 0 0;
  font-size: 0.9rem;
  opacity: 0.9;
}

.truncated {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>
